@import "./_resets.scss";
@import "./mixins.scss";
@import "./_variables.scss";
@import "./watches.scss";




.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: gray;

}



@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }

}
